.container {
  width: 100%;

  .box {
    width: 100%;

    .tip {
      font-size: 12px;
      color: #adadad;
    }

    :global {
      .adm-input {
        padding: 10px;
        background-color: #f3f3f38e;
        border-radius: 5px;
        width: 100%;
        margin: 20px auto;
        box-sizing: border-box;
      }

      .adm-input-element {
        color: #6d6d6d;
      }

      .adm-button-block {
        margin: 20px auto;
      }
    }
  }
}
@primary-color: #1DA57A;