.container {
  width: 100%;

  :global {
    .adm-input {
      padding: 10px;
      background-color: #f3f3f38e;
      border-radius: 5px;
      width: 100%;
      margin: 20px auto;
      box-sizing: border-box;
    }

    .adm-input-element {
      color: #6d6d6d;
    }
  }

  .info {
    margin-top: 50px;
    width: 100%;
    height: 500px;

    p {
      margin-bottom: 0;
      font-size: 14px;
    }

    .cover {
      width: 100%;
      position: relative;
    }

    .playico {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

  }
}
@primary-color: #1DA57A;